import "./List.css";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import './RangeSlider.css';
import { setDate, setTimeRedux, setYo, setMicro, setWeird } from "./store";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faWhiskeyGlass, faGlassWater, faBottleWater,  faMicrophone, faMicrophoneAlt } from "@fortawesome/free-solid-svg-icons";

function List2({ className }) {
    const dispatch = useDispatch();
    const inputRef = useRef(null);
    const inputRef2 = useRef(null);
    const inputRef3 = useRef(null);

    const date1 = useSelector((state) => state.date);
    const timeR = useSelector((state) => state.time);
    const weird = useSelector((state) => state.weird);
  
    const [dateValue, setDateValue] = useState(date1 || ""); // Local state for date input
    const [timeValue, setTimeValue] = useState(timeR || ""); // Local state for time input
    const [microphone, setMicrophone] = useState(false);
    const [value, setValue] = useState(0);  // Initial value for the score slider

    const yo = useSelector((state) => {
      return state.yo;
    });
    // Update local state on mount
    useEffect(() => {
      // If date1 is available, set it as the initial value
      if (date1) {
          setDateValue(date1);
      } else {
          //     (: YYYY-MM-DD )
          const today = new Date();
          const formattedDate = today.toISOString().split('T')[0];
          setDateValue(formattedDate);
          dispatch(setDate(formattedDate)); //    
      }

      // If timeR is available, set it as the initial value
      if (timeR) {
          setTimeValue(timeR);
          //inputRef.
      }
    }, [date1, timeR, dispatch]);

    // Handle date change
    const handleDateChange = (e) => {
        const newDate = e.target.value;
        setDateValue(newDate);
        dispatch(setDate(newDate));
    };

    // Handle time change
    const handleTimeChange = (e) => {
        const newTime = e.target.value;
        setTimeValue(newTime);
        dispatch(setTimeRedux(newTime));
    };


    const handleSliderChange = (event) => {
      setValue(parseInt(event.target.value,10));
      inputRef3.current.value = parseInt(event.target.value,10);
      dispatch(setYo(event.target.value));
    }


    const useMicro = (e) => {
      setMicrophone(true);
      dispatch(setMicro(true));
    }

    const handleInputChange = (event) => {
      dispatch(setWeird(event.target.value)); 
    };


    return (
      <div className={`List silgeum ${className}`}>
        <h5 className="title">  </h5>
        <div className="elementBtn">
          <span className="smallTitle"></span>
          <input 
            type="date" 
            className="styledDateInput" 


            value={date1.value} 


            onChange={handleDateChange} 
            ref={inputRef2} 
          />
        </div>
        <div className="elementBtn">
          <span className="smallTitle"></span>
          <input 
            type="time" 
            className="styledDateInput" 

            value={timeR.value} 
            onChange={handleTimeChange} 
            ref={inputRef} 
          />
        </div>
        <div className="elementBtn">
          <span className="smallTitle"> </span>
          <input type="number" className="inputY inputY2" defaultValue={0} value={yo.value} ref={inputRef3} />
          <span className="smallTitle"></span>
        </div>
        <div className="elementBtn3">
          <input
            type="range"
            min="0"
            max="3"
            step="1"
            value={yo.value}
            onChange={handleSliderChange}
            className="slider"
            style={{width: '95%'}}
          />
        </div>
        <div className="elementBtn3">
          <div className="labels">
            <span className={`label ${value === 0 ? 'active' : ''}`} style={{fontSize: "10px"}}>0</span>
            <span className={`label ${value === 1 ? 'active' : ''}`} style={{fontSize: "10px"}}>1</span>
            <span className={`label ${value === 2 ? 'active' : ''}`} style={{fontSize: "10px"}}>2</span>
            <span className={`label ${value === 3 ? 'active' : ''}`} style={{fontSize: "10px"}}>3</span>
          </div>
        </div>
        <div className="elementBtn">
          <div className="labels2">
            <span className={`label labelLeft active2 ${value === 0 ? 'active' : ''}`}></span>
            <span className={`label labelRight active2 ${value === 3 ? 'active' : ''}`}> </span>
          </div>
        </div>
        <div className="elementBtn3">
          <span className="smallTitle"> ()</span>
        </div>
        <div className="elementBtn">
          <input className="inputY inputY4" value={weird.value} onChange={handleInputChange} />
          <FontAwesomeIcon icon={faMicrophoneAlt} style={{color : 'black'}} onClick={useMicro}/> 
        </div>
      </div>
    );
}

export default List2;

